import React, { Component } from "react";
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
class Testimonial extends Component{
    render(){
        return(
            <React.Fragment>
                <div className="row">
                    <div className="col-lg-12">
                        <Tabs>
                            <TabPanel>
                                <div className="rn-testimonial-content text-center">
                                    <div className="inner">
                                        <p>Stránku som potreboval aby čo najlepšie odprezentovala moje služby pred klientami v zahraničí. Webclientu sa to podarilo nadmieru kvalitne splniť a ešte aj v prekvapivo <strong className="theme-gradient">krátkom čase.</strong></p>
                                    </div>
                                    <div className="author-info">
                                        <h6><span>Dominik Šalata </span> - SALDOM S.R.O</h6>
                                    </div>
                                </div>
                            </TabPanel>

                            <TabPanel>
                                <div className="rn-testimonial-content text-center">
                                    <div className="inner">
                                        <p>Chalani sa so stránkou naozaj vyhrali, animácie, tiene, proste všetko na čo si len pomyslíte. <br/> <strong className="theme-gradient">Kvalita za dobrú cenu.</strong>  </p>
                                    </div>
                                    <div className="author-info">
                                        <h6><span>Jozef Babinský </span> - STROJOVÉ POTERY JOZEF BABINSKÝ</h6>
                                    </div>
                                </div>
                            </TabPanel>
                            <TabPanel>
                                <div className="rn-testimonial-content text-center">
                                    <div className="inner">
                                        <p>Mnoho zákazníkov si pýtalo kontakt na mňa prostredníctvom sociálnych sietí. Tak som sa rozhodol, že informácie zhrniem na jednom mieste. <br/> <strong className="theme-gradient">Webstránke od Webclientu !</strong></p>
                                    </div>
                                    <div className="author-info">
                                        <h6><span>RÓBERT KMEŤ </span> - WOODLAND.SK</h6>
                                    </div>
                                </div>
                            </TabPanel>

                            <TabPanel>
                                <div className="rn-testimonial-content text-center">
                                    <div className="inner">
                                        <p>V dnešnej dobre je webstránka pomaly nutnosť pri podnikaní. Preto som rád, že som to zveril Webclientu a som <strong className="theme-gradient">nadmieru spokojný.</strong></p>
                                    </div>
                                    <div className="author-info">
                                        <h6><span>TOMÁŠ MURÍN </span> - NEREZOVÁ VÝROBA TOMÁŠ MURÍN</h6>
                                    </div>
                                </div>
                            </TabPanel>
                            
                            <TabList className="testimonial-thumb-wrapper">
                                <Tab>
                                    <div className="testimonial-thumbnai">
                                        <div className="thumb">
                                            <img src="/assets/images/client/testimonial-1.jpg" alt="Testimonial Images"/>
                                        </div>
                                    </div>
                                </Tab>
                                <Tab>
                                    <div className="testimonial-thumbnai">
                                        <div className="thumb">
                                            <img src="/assets/images/client/testimonial-2.jpg" alt="Testimonial Images"/>
                                        </div>
                                    </div>
                                </Tab>
                                <Tab>
                                    <div className="testimonial-thumbnai">
                                        <div className="thumb">
                                            <img src="/assets/images/client/testimonial-3.jpg" alt="Testimonial Images"/>
                                        </div>
                                    </div>
                                </Tab>
                                <Tab>
                                    <div className="testimonial-thumbnai">
                                        <div className="thumb">
                                            <img src="/assets/images/client/testimonial-4.jpg" alt="Testimonial Images"/>
                                        </div>
                                    </div>
                                </Tab>
                            </TabList>
                        </Tabs>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}
export default Testimonial;