import React, { Component } from "react";

class About extends Component{
    render(){
        let title = 'O nás',
        description = 'Sme nová inovatívna firma, zaoberajúca sa tvorbou webových stránok, e-shopov, vývojom webových, či mobilných aplikácií.\n Ďalej do nášho spektra služieb patrí optimalizácia pre vyhľadávacie nástroje, marketingový mentoring a v neposlednom rade kompletná škála grafických služieb.';
        return(
            <React.Fragment>
                <div className="about-wrapper">
                    <div className="container">
                        <div className="row row--35 align-items-center">

                            <div className="col-lg-5 col-md-12">
                                <div className="thumbnail">
                                    <img className="w-100" src="/assets/images/about/about-1.jpg" alt="About Images"/>
                                </div>
                            </div>

                            <div className="col-lg-7 col-md-12">
                                <div className="about-inner inner">
                                    <div className="section-title">
                                        <h2 className="title theme-gradient">{title}</h2>
                                        <p className="description">{description}</p>
                                    </div>
                                    
                                    <div className="row mt--30 mt_sm--10">
                                        <div className="col-lg-6 col-md-12 col-sm-12 col-12">
                                            <div className="about-us-list">
                                                <h3 className="title ">Vek je len číslo</h3>
                                                <p>To, že sme na trhu tak krátko, ešte neznamená že nemáme žiadne skúsenosti a znalosti. Práve naopak, prinášame moderné riešenia a služby svetových kvalít.</p>
                                            </div>
                                        </div>
                                        <div className="col-lg-6 col-md-12 col-sm-12 col-12">
                                            <div className="about-us-list">
                                                <h3 className="title ">Držíme s dobou</h3>
                                                <p>Využívame najnovšie technológie a aktuálne dizajnové trendy pre vytvorenie <strong className="theme-gradient">absolútnej spokojnosti</strong>  nášho klienta. </p>
                                            </div>
                                        </div>
                                        <div className="service-btn about-btn">
                                            <a className="btn-transparent rn-btn-dark" href="/about"><span className="text">Viac informácií... </span></a>
                                        </div>  
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}
export default About;