import React, { Component } from "react";
import PageHelmet from "../../component/common/Helmet";
import Breadcrumb from "../../component/common/Breadcrumb";
import Counter from "../../component/counter/Counter";
import Testimonial from "../../component/testimonial/Testimonial";
import Brand from "../../component/brand/Brand";
import { FaFacebookF , FaLinkedinIn , FaTwitter } from "react-icons/fa";
import ScrollToTop from 'react-scroll-up';
import { FiChevronUp } from "react-icons/fi";
import Header from "../../component/header/Header";
import Footer from "../../component/footer/Footer";

import {
    FaReact,
    FaSass
} from "react-icons/fa";
import {
    FiSmartphone,
    FiCode,
    FiDownload,
    FiCommand,
    FiHeadphones,
    FiBold
} from "react-icons/fi";


const featureList = [
    {
        icon: <FaReact/>,
        title: 'React 16.8+',
        subtitle: 'Používame najnovšiu verziu Reactu 16.8.6. pre dokonalé výsledky.'
    },
    {
        icon: <FiSmartphone/>,
        title: 'Plne Responzívny',
        subtitle: 'Naše produkty sú vytvárané tak, aby boli perfektne zobrazované naakomkoľvek zariadení.'
    },
    {
        icon: <FiCode/>,
        title: 'Prehľadný kód',
        subtitle: 'Vo webcliente sa staráme nielen o funkčnosť kódu, ale aj o jeho čitateľnoť.'
    },
    {
        icon: <FaSass/>,
        title: 'Sass ',
        subtitle: 'Sass nám umožňuje rôznorodo upravovať a dynamicky generovať štylizáciu stránky.'
    },
    {
        icon: <FiDownload/>,
        title: 'Bleskovo rýchly',
        subtitle: 'Čas sú peniaze, preto si naše stránky vyžadujú minimálnu dobu potrebnú pre načítanie.'
    },
    {
        icon: <FiCommand/>,
        title: 'Moderný Dizajn',
        subtitle: 'Naši dizajnéri si vždy dajú záležať, aby výsledný produkt lahodil oku používateľa.'
    },
    {
        icon: <FiHeadphones/>,
        title: '24h Podpora',
        subtitle: 'Ponúkame 24 hodinovú technickú podporu pre našich klientov.'
    },
    {
        icon: <FiBold/>,
        title: 'Bootstrap 5.0',
        subtitle: 'Bootstrap nám neraz ušetril kopec námahy a je veľmi šikovný pomocník najmä pri veľkých projektoch.'
    }
]

class About extends Component{
    render(){
        let title = 'O nás',
        description = 'There are many variations of passages of Lorem Ipsum available, but the majority have suffered alteration in some form, by injected humour, or randomised words which dont look even slightly believable. If you are going to use a passage of Lorem Ipsum Ipsum available, but the majority have suffered alteration in some form, by injected humour, or randomised words which dont look even slightly believable. If you are going.';
        return(
            <React.Fragment>
                <PageHelmet pageTitle='O nás' />

                <Header headertransparent="header--transparent" colorblack="color--black" logoname="logo.png" />
                {/* Start Breadcrump Area */}
                <Breadcrumb title={'O nás'}   />
                {/* End Breadcrump Area */}

                {/* Start About Area  */}
                <div className="rn-about-area ptb--120 bg_color--1">
                    <div className="rn-about-wrapper">
                        <div className="container">
                            <div className="row row--35 align-items-center">
                                <div className="col-lg-5">
                                    <div className="thumbnail">
                                        <img className="w-100" src="/assets/images/about/us.png" alt="About Images"/>
                                    </div>
                                </div>
                                <div className="col-lg-7 col-md-12">
                                    <div className="about-inner inner">
                                        <div className="section-title">
                                            <h2 className="title theme-gradient">{title}</h2>
                                            <p className="description">{description}</p>
                                        </div>
                                        
                                        <div className="row mt--30 mt_sm--10">
                                            <div className="col-lg-6 col-md-12 col-sm-12 col-12">
                                                <div className="about-us-list">
                                                    <h3 className="title ">Vek je len číslo</h3>
                                                    <p>To, že sme na trhu tak krátko, ešte neznamená že nemáme žiadne skúsenosti a znalosti. Práve naopak, prinášame moderné riešenia a služby svetových kvalít.</p>
                                                </div>
                                            </div>
                                            <div className="col-lg-6 col-md-12 col-sm-12 col-12">
                                                <div className="about-us-list">
                                                    <h3 className="title ">Držíme s dobou</h3>
                                                    <p>Využívame najnovšie technológie a aktuálne dizajnové trendy pre vytvorenie absolútnej spokojnosti nášho klienta. </p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                {/* End About Area  */}

                {/* Start Feature Area  */}
                 <div className="prv-feature service-area bg_color--4 ptb--120"> 
                        <div className="wrapper plr--120">
                            <div className="row">
                                <div className="col-lg-12">
                                    <div className="section-title text-center pb--30">
                                        <h2 className="text-white">Ponúkame výhody</h2>
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                {/* Start Single Feature  */}
                                {featureList.map((value, i) => (
                                    <div className="col-lg-6 col-xl-3 col-md-6 col-sm-6 col-12" key={i}>
                                        <div className="service service__style--2">
                                            <div className="icon">
                                                {value.icon}
                                            </div>
                                            <div className="content">
                                                <h3 className="title">{value.title}</h3>
                                                <p className="subtitle">{value.subtitle}</p>
                                            </div>
                                        </div>
                                    </div>
                                ))}
                                {/* End Single Feature  */}
                            </div>
                        </div>
                    </div>
                    {/* End Feature Area  */}

                 {/* Start CounterUp Area */}
                 <div className="rn-counterup-area pb--120 bg_color--1 pt--120">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="section-title text-center">
                                    <h3 className="fontWeight500">Čísla hrajú v náš prospech</h3>
                                </div>
                            </div>
                        </div>
                        <Counter />
                    </div>
                </div>
                {/* End CounterUp Area */}

                {/* Start Finding Us Area  */}
                <div className="rn-finding-us-area rn-finding-us bg_color--1">
                    <div className="inner">
                        <div className="content-wrapper">
                            <div className="content">
                                <h4 className="theme-gradient">Chceš byť súčasťou nášho tímu?</h4>
                                <p>Si vášnivý programátor a ovládaš technológie ako HTML5, CSS3 a Javasript ? Chcel by si byť pri zrode novej progresívnej firmy ? <br/>
                                Potom neváhaj a napíš nám. Všetko potrebné ťa doučíme a zasvätíme ťa do praxe. Už sa tešíme na novú posilu. :)</p>
                                <a className="rn-btn btn-white" href="/about">Poslať žiadosť</a>
                            </div>
                        </div>
                        <div className="thumbnail">
                            <div className="image">
                                <img src="/assets/images/about/finding-us-01.png" alt="Finding Images"/>
                            </div>
                        </div>
                    </div>
                </div>
                {/* End Finding Us Area  */}

                {/* Start Team Area  */}
                <div className="rn-team-area bg_color--1 ptb--120">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="section-title service-style--3 text-center mb--25">
                                    <h2 className="title">Náš tím</h2>
                                    <p>Sme skupinka mladých ľudí, ktorí sa neprestajne zdokonaľujú vo svojom obore.</p>
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            
                            {/* Start Single Team  */}
                            <div className="col-lg-4 col-md-6 col-sm-6 col-12">
                                <div className="team">
                                    <div className="thumbnail">
                                        <img className="w-100" src="/assets/images/team/team-01.jpg" alt="Blog Images"/>
                                    </div>
                                    <div className="content">
                                        <h4 className="title">Denis Kurčina</h4>
                                        <p className="designation">Marketing</p>
                                    </div>
                                    <ul className="social-icon" >
                                        <li><a href="https://www.facebook.com/"><FaFacebookF /></a></li>
                                        <li><a href="http://linkedin.com/"><FaLinkedinIn /></a></li>
                                        <li><a href="https://twitter.com/"><FaTwitter /></a></li>
                                    </ul>
                                </div>
                            </div>
                            {/* End Single Team  */}

                            {/* Start Single Team  */}
                            <div className="col-lg-4 col-md-6 col-sm-6 col-12">
                                <div className="team">
                                    <div className="thumbnail">
                                        <img className="w-100" src="/assets/images/team/team-02.jpg" alt="Blog Images"/>
                                    </div>
                                    <div className="content">
                                        <h4 className="title">Pavol Jurčák</h4>
                                        <p className="designation">Developer</p>
                                    </div>
                                    <ul className="social-icon" >
                                        <li><a href="https://www.facebook.com/"><FaFacebookF /></a></li>
                                        <li><a href="http://linkedin.com/"><FaLinkedinIn /></a></li>
                                        <li><a href="https://twitter.com/"><FaTwitter /></a></li>
                                    </ul>
                                </div>
                            </div>
                            {/* End Single Team  */}

                            {/* Start Single Team  */}
                            <div className="col-lg-4 col-md-6 col-sm-6 col-12">
                                <div className="team">
                                    <div className="thumbnail">
                                        <img className="w-100" src="/assets/images/team/team-03.jpg" alt="Blog Images"/>
                                    </div>
                                    <div className="content">
                                        <h4 className="title">Jana Krivá</h4>
                                        <p className="designation">SEO optimalizácia</p>
                                    </div>
                                    <ul className="social-icon" >
                                        <li><a href="https://www.facebook.com/"><FaFacebookF /></a></li>
                                        <li><a href="http://linkedin.com/"><FaLinkedinIn /></a></li>
                                        <li><a href="https://twitter.com/"><FaTwitter /></a></li>
                                    </ul>
                                </div>
                            </div>
                            {/* End Single Team  */}

                        </div>
                    </div>
                </div>
                {/* End Team Area  */}

                {/* Start Testimonial Area */}
                <div className="rn-testimonial-area bg_color--5 ptb--120">
                    <div className="container">
                        <Testimonial />
                    </div>
                </div>
                {/* End Testimonial Area */}

                {/* Start Brand Area */}
                <div className="rn-brand-area brand-separation bg_color--5 ptb--120">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <Brand />
                            </div>
                        </div>
                    </div>
                </div>
                {/* End Brand Area */}

                {/* Start Back To Top */}
                <div className="backto-top">
                    <ScrollToTop showUnder={160}>
                        <FiChevronUp />
                    </ScrollToTop>
                </div>
                {/* End Back To Top */}
                
                <Footer />

            </React.Fragment>
        )
    }
}
export default About