import React, { Component } from "react";
import PageHelmet from "../../component/common/Helmet";
import Breadcrumb from "../../component/common/Breadcrumb";
import {  FiUsers , FiMonitor ,FiChevronUp, FiShoppingCart,  FiHeadphones, FiImage, FiSettings, FiPenTool, FiBookmark, FiSliders } from "react-icons/fi";
import ScrollToTop from 'react-scroll-up';
import Header from "../../component/header/Header";
import Footer from "../../component/footer/Footer";




const WebList = [
    {
        icon: <FiMonitor />,
        title: 'Prezenčná stránka',
        description: 'Jednoduchá webstránka so všetkým, čo potrebujete pre opis Vášho podnikania.',
        price:'od 350€'
    },
    {
        icon: <FiHeadphones />,
        title: 'Komunikačná stránka',
        description: 'Potrebujete byť v neustálom styku so zákazníkom prostredníctvom blogu alebou online chatu?',
        price:'od 500€'
    },
    {
        icon: <FiShoppingCart />,
        title: 'Internetový obchod',
        description: 'Ponúkame dôkladné predstavenie produktu, prepracovaný systém objednávok a ešte omnoho viac.',
        price:'od 750€'
    }
]

const DesignList = [
    {
        icon: <FiImage />,
        title: 'Tvorba Loga',
        description: 'I throw myself down among the tall grass by the stream as I lie close to the earth.',
        price:'od 100€'
    },
    {
        icon: <FiPenTool />,
        title: 'Návrh vizitiek a iných produktov',
        description: 'I throw myself down among the tall grass by the stream as I lie close to the earth.',
        price:'od 50€'
    },
    {
        icon: <FiUsers />,
        title: 'Kompletná virtuálna identita',
        description: 'I throw myself down among the tall grass by the stream as I lie close to the earth.',
        price:'od 150€'
    },
  
]

const MarketingList = [
    {
        icon: <FiSettings />,
        title: 'SEO-optimalizácia',
        description: 'I throw myself down among the tall grass by the stream as I lie close to the earth.',
        price:'od 70€'
    },
    {
        icon: <FiBookmark />,
        title: 'Reklamný návrh',
        description: 'Navrhneme Vám propagačný príspevok na sociálne siete alebo reklamný spot pre investorov.',
        price:'od 100€'
    },
    {
        icon: <FiSliders />,
        title: 'Biznis plán',
        description: 'Rozbiehate novú firmu a nemáte žiadne znalosti? Máme pre vás riešenie - Náš Biznis plán.',
        price:'od 170€'
    },
    
]
class Service extends Component{
    render(){
        return(
            <React.Fragment>
                <PageHelmet pageTitle='Služby' />
                <Header headertransparent="header--transparent" colorblack="color--black" logoname="logo.png" />

                {/* Start Breadcrump Area */}
                <Breadcrumb title={'Služby'}   />
                {/* End Breadcrump Area */}

                {/* Start Service Area */}
                <div className="service-area ptb--120 bg_color--5">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="section-title text-center mb--30">
                                    <h2>Tvorba webstránok</h2>
                                    <p>Tvorenie webových stránok patrí medzi naše hlavné zameranie, preto veríme, <br />  že aj vy zveríte svoju stránku do našich rúk.</p>
                                </div>
                            </div>
                        </div>
                        <div className="row service-one-wrapper">
                            {WebList.map( (val , i) => (
                                <div className="col-xl-4 col-lg-4 col-md-6 col-sm-6 col-12" key={i}>
                                    <a href="/service-details">
                                        <div className="service service__style--2">
                                            <div className="icon">
                                                {val.icon}
                                            </div>
                                            <div className="content">
                                                <h3 className="title">{val.title}</h3>
                                                <p>{val.description}</p>
                                                <p>{val.price}</p>
                                            </div>
                                        </div>
                                    </a>
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
                {/* End Service Area */}

                {/* Start Service Area */}
                <div className="service-area ptb--120 bg_color--1">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="section-title text-center mb--30">
                                    <h2>Grafické služby</h2>
                                    <p>Precíznosť a zmysel pre detail sú alfou a omegou spravneho dizajnéra. <br /> Tím naším to samozrejme nechýba :)</p>
                                </div>
                            </div>
                        </div>
                        <div className="row service-one-wrapper">
                            {DesignList.map( (val , i) => (
                                <div className="col-xl-4 col-lg-4 col-md-6 col-sm-6 col-12" key={i}>
                                    <a className="text-center" href="/service-details">
                                        <div className="service service__style--2">
                                            <div className="icon">
                                                {val.icon}
                                            </div>
                                            <div className="content">
                                                <h3 className="title">{val.title}</h3>
                                                <p>{val.description}</p>
                                                <p>{val.price}</p>
                                            </div>
                                        </div>
                                    </a>
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
                {/* End Service Area */}

                {/* Start Service Area */}
                <div className="service-area creative-service-wrapper pt--90 pb--120 bg_color--5">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="section-title text-center mb--30">
                                    <h2>Online marketing</h2>
                                    <p>Nedarí sa vášmu biznisu aj napriek tomu, že máte lukratívny produkt ? <br /> Problém bude .</p>
                                </div>
                            </div>
                        </div>
                        <div className="row creative-service">
                            {MarketingList.map( (val , i) => (
                                <div className="col-xl-4 col-lg-4 col-md-6 col-sm-6 col-12" key={i}>
                                    <a href="/service-details">
                                        <div className="service service__style--2">
                                            <div className="icon">
                                                {val.icon}
                                            </div>
                                            <div className="content">
                                                <h3 className="title">{val.title}</h3>
                                                <p>{val.description}</p>
                                                <p>{val.price}</p>
                                            </div>
                                        </div>
                                    </a>
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
                {/* End Service Area */}

                {/* Start Back To Top */}
                <div className="backto-top">
                    <ScrollToTop showUnder={160}>
                        <FiChevronUp />
                    </ScrollToTop>
                </div>
                {/* End Back To Top */}
                
                <Footer />


            </React.Fragment>
        )
    }
}
export default Service;